.wrap {
  position: relative;
}

.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  height: 80px;
  z-index: 1111;
}

.header .logo {
  position: absolute;
  left: 60px;
  top: 20px;
}

.header .util {
  position: absolute;
  right: 60px;
  top: 20px;
}

.header .util .util_txt {
  font-size: 16px;
  color: #000;
  font-weight: 700;
  margin-right: 10px;
}

.header .util .btn_round {
  margin-left: 10px;
  height: 36px;
  border-radius: 18px;
  text-align: center;
  width: 120px;
  border: 1px solid #000;
  display: inline-block;
}

.header .util .btn_round span {
  position: relative;
  line-height: 36px;
  font-size: 14px;
  color: #000;
  display: inline-block;
  padding-left: 22px;
}

.header .util .btn_round span:after {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  margin-top: -9px;
  width: 15px;
  height: 18px;
}

.header .util .btn_ios span:after {
  background: url("/resources/images/intro/logo_ios_sm.png");
}

.header .util .btn_android span:after {
  background: url("/resources/images/intro/logo_android_sm.png");
}

.toyou_wrap {
  background: url("/resources/images/intro/pc_bg.jpg");
  background-size: 100%;
  transition: 1s cubic-bezier(0.3, 0, 0.3, 1);
  background-repeat: no-repeat;
}

.sm_section {
  position: relative;
}

.sm_section .sm_inner {
  margin: 0 auto;
  width: 1000px;
  position: relative;
  height: 100%;
}

.sm_section .sm_inner .sm_tit {
  font-size: 50px;
  font-weight: 700;
  color: #000;
  line-height: 62px;
}

.sm_section .sm_inner .sm_tit b {
  color: #ff5c5c;
}

.sm_section .sm_inner .sm_txt {
  font-size: 26px;
  font-weight: 500;
  color: #000;
  line-height: 40px;
}

.sm_section .sm_inner .sm_txt_sm {
  font-size: 20px;
  font-weight: 500;
  color: #000;
}

.sm_section .sm_inner .btn_box {
  width: 550px;
  margin: 0 auto;
}

.sm_section .sm_inner .btn_round {
  height: 60px;
  border-radius: 30px;
  text-align: center;
  width: 270px;
  background: #fff;
  display: block;
  float: left;
  box-shadow: 5px 5px 20px 0 rgba(0, 0, 0, 0.05), 5px 5px 20px 0 rgba(0, 0, 0, 0.05);
}

.sm_section .sm_inner .btn_round:first-child {
  margin-right: 10px;
}

.sm_section .sm_inner .btn_round span {
  position: relative;
  line-height: 60px;
  font-size: 16px;
  color: #000;
  display: inline-block;
  padding-left: 36px;
}

.sm_section .sm_inner .btn_round span:after {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  margin-top: -14px;
  width: 24px;
  height: 27px;
}

.sm_section .sm_inner .btn_ios span:after {
  background: url("/resources/images/intro/logo_ios.png");
}

.sm_section .sm_inner .btn_android span:after {
  background: url("/resources/images/intro/logo_android.png");
}

.sm_section .descript.pos_md {
  position: absolute;
  top: 50%;
}

.partners_list {
  text-align: center;
}

.partners_list:after {
  content: '';
  display: block;
  clear: both;
}

.partners_list li {
  display: inline-block;
